
.anm-enter-active[data-v-484f1ba2]{
		opacity: 1;
}
.anm-enter[data-v-484f1ba2],.anm-leave-active[data-v-484f1ba2]{
		opacity: 0;
		-webkit-transform: scale(0.7);
		        transform: scale(0.7);
}
.jack-popUp[data-v-484f1ba2]{
		position: fixed;
		top: 0;
		left: 0;
		z-index: 999;
		width: 100%;
		height: 100%;
		background: rgba(33, 33, 33, 0.75);
		-webkit-transition: all 0.4s;
		transition: all 0.4s;
}
.popUp-box[data-v-484f1ba2]{
		position: fixed;
		z-index: 2000;
		top: 50%;
		left: 50%;
		transform: translateY(-50%) translateX(-50%);
   		-webkit-transform: translateY(-50%) translateX(-50%);
}
.popUp-box i[data-v-484f1ba2]{
		display: inline-block;
		width: 1.5rem;
		height: 1.5rem;
		background: url(/static/tiny/close.svg);
		background-size: 1.5rem;
		position: absolute;
		top: 0.5rem;
		right: 1rem;
}	



